import React, {Component} from 'react';
class MessageInput extends Component{

  constructor(props){
    super(props);
    this.state={
        text:""
    };
  }

  render(){
    return (<div>
        <div className="box-footer">
                <form onSubmit={(e)=>{
                  e.preventDefault();
                  this.sendMessage();
                }}>
                    <div className="input-group">
                        <input id="chat-input" type="text" name="message" 
                        placeholder="Type Message ..." className="form-control" value={this.state.text}
                        onChange={(e)=>this.setState({text:e.target.value})}
                        autoComplete="off"/> 
                        <span className="input-group-btn">
                        <button  type="submit" className="btn btn-primary btn-flat" 
                        style={{padding:"6px 10px",borderRadius:"2px 2px 2px 0px"}}>Send</button>
                        </span> </div>
                </form>
            </div>
    </div>);
  }

  sendMessage(){
    let obj={
      mediaUrl:"",
      message:this.state.text, 
      name:this.props.firebase.auth().currentUser.name||atob(localStorage.getItem("token")).split(":")[0].split("@")[0],
      photoUrl:"",
      sender:atob(localStorage.getItem("token")).split(":")[0],
      timestamp:Date.now()
    }
    console.log(obj);  
    let db=this.props.firebase.database();
    let messagesRef=db.ref(this.props.domain+"/messages/"+this.props.currentChatId);
    let chatRef=db.ref(this.props.domain+"/chats/"+this.props.currentChatId);
    messagesRef.push(obj);
    chatRef.child('lastmessage').set(this.state.text);
    chatRef.child('timestamp').set(obj.timestamp);
    this.setState({text:""});
  }

}

export default MessageInput;


