import React, { Component } from 'react';
import ChatListBox from './../../chatwindow/ChatLists/ChatListBox';
const $ =window.$;
export default class ReactChatContainer extends Component {

      constructor(props){
          super(props);
          this.state={
              chatList:[{jobId: 4545454587887, lastmessage: "hi", timestamp: 1521231231500, title: "demo title"}]
          };
      }  

      
  getStatus(obj){
    if(obj.chatId==this.props.currentChatId)
    return "active";
    else return "";
  }


  render(){
    return (
        <div className="recent-chat-list dropdown-menu" aria-labelledby="dLabel">
                   <div className="header">
                      <strong>Recent (12)</strong>
                      <span className="grey_col">Message Requests</span>
                       <div className="pull-right">
                       <a href="#/chats"> New group </a> 
                       <a href="#/chats"> New message </a>
                       </div>
                   </div>
                   
                   <div className="row no-mrg recent-chat-container">
                      <div className="col-xs-12 no-padd">
                      {
                           this.state.chatList.map((obj,index)=>{
                             return <ChatListBox chatStatus={this.getStatus(obj)} 
                            //   updateCurrentChat={this.props.updateCurrentChat}  
                             chat={obj} key={index}/>})
                      }
                      {
                          //  to be implemented
                           /* <ChatListBox chatStatus="new"/>
                         <ChatListBox chatStatus="active"/>
                        */}
                       </div>
                       
                   </div>
                   <div className="header">
                       <a href="#/chats">See all in messenger</a>
                       <a className="pull-right">Mark all as read</a>
                   </div>
               </div>
   )
}
}


