import React, { Component } from 'react';
const $ =window.$;
export default class NewToDo extends Component {

      constructor(props){
          super(props);
          this.state={
          };
      }  


  render(){
    return (
         
        <div className="new_todo none">
        <h3 className="text-center">Add New To-do</h3>
         <form>
         <div className="form-group">
         <label htmlFor="todo">ToDo Name</label>
          <input type="text"  className="form-control" placeholder="name"/>
         </div>
           <div className="form-group">
         <label htmlFor="todo">ToDo Description</label>
          <input type="text"  className="form-control"  placeholder="description"/>
         </div>
           <div className="form-group">
         <label htmlFor="todo">Date</label>
          <input type="date"  className="form-control"/>
         </div>
           
           <div className="form-group">
         <label htmlFor="todo">Due Date</label>
          <input type="date"  className="form-control"/>
         </div>
           
           
            <div className="text-center">
            <a className="btn btn-primary" style={{"padding":"6px 15px"}}>Add</a>
           </div>
         </form> 
      </div>
    );
  }
}  


