import React, { Component } from 'react';
import ToDoBox from './ToDo';
import NewToDo from './NewToDo';
const $ =window.$;
export default class ToDoContainer extends Component {

      constructor(props){
          super(props);
          this.state={
          };
      }  

      
  getStatus(obj){
    if(obj.chatId==this.props.currentChatId)
    return "active";
    else return "";
  }


  render(){
    return (
        <div className="tasks col-sm-4">
         <div className="tasks_container bg-white">
             <div className="todo_container">
              <h3 className="text-center">Todo List</h3>
                <div className="to-dos">
                 <ToDoBox border="blue"/>
                 <ToDoBox border="red"/>
                 <ToDoBox border="blue"/>
                 <ToDoBox border="green"/>
                 <ToDoBox border="yellow"/>       
                 <ToDoBox border="green"/>
                 <ToDoBox border="yellow"/>
                 </div>        
                 </div>
                </div>
                <span className="add_btn"><i className="fa fa-plus"></i></span>
                <NewToDo/>
         </div>
   );
}

componentDidMount(){
    let a=()=>{
        if($(window).width()>640)
        $("header .recent-chat-list").css("height",$(window).height()*0.67+"px");
        $(".to-dos").css("max-height",$(window).height()-155+"px");
    };
    a();
        $(".add_btn , .new_todo a").click(function(){
            $(".new_todo").toggleClass("none");
            //add to resize too
        });

        $(".task_bar").click(function(){
            $(".tasks").toggle();
        $(".to-dos").css("max-height",$(window).height()-155+"px");
        });
    window.onresize=a;
}
}


