import React,{Component} from  'react';
import Resume from './../resume';
import CandidateDetail from './candidateDetails';
const $=window.$;
export default class CandidateInfo extends Component{

    constructor(props){
        super(props);
        this.state={
            activeTab:"details",
            mode:$(window).width()<=768?"modal":"box"
        };
    }

    findFinalStatusDate(statusList){
      console.log(statusList)
      let finalStatus = statusList[statusList.length - 1].status,
         date=statusList[statusList.length-1].Timestamp;
         finalStatus=finalStatus.toString();
         if((finalStatus===""||finalStatus == null||finalStatus=="NaN"||finalStatus.includes("null"))
     && statusList.length>1){
             finalStatus = statusList[statusList.length - 2].status;
             date=statusList[statusList.length-2].Timestamp;
         }
         return {finalStatus,date};
    }

    render(){
        let candidate=this.props.currentCandidate;
        if(!candidate.work_experience)
        {
          candidate.work_experience=[{
            organization:"",
            role:""
        }];}
        // let sortedAddedArr=[];
        // console.log(candidate);
        // if(!candidate.added_by[0].status)
        // return null;
        // if(candidate.added_by.length>1)
        // sortedAddedArr=candidate.added_by.sort((x,y)=>{
        //   return this.findFinalStatusDate(x.status).date-this.findFinalStatusDate(y.status).date;
        // });
        // else sortedAddedArr=candidate.added_by;
        // let matchObj=sortedAddedArr[0];  
        //   //  console.log(sortedAddedArr)
        //  let statusList = matchObj.status;
        //  let {finalStatus,date} = this.findFinalStatusDate(statusList);
        return (
        <div>
          {this.state.mode=="modal"?
          (       
          <div className="modal fade" id="cand-info_modal" tabIndex="32"  role="dialog" aria-labelledby="myLabel">
           <div className="modal-dialog" role="document">
             <div className="modal-content">
               <div className="modal-header" style={this.state.type?{padding:"5px 15px"}:{}}>
                 <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                 <h4 className="modal-title" id="myModalLabel">Candidate Modal</h4>
               </div> 
                <div className="modal-body no-padd">
                  <div style={{background:"#eee"}}>
                     <div className="bg-white row info">
                      <div className="pull-left">
                          <h4>{candidate.candidateName}</h4>
                          <h5>{candidate.work_experience[0].organization}</h5>
                          <h5>{candidate.location}</h5>
                      </div>
                      <div className="pull-right">
                          {/* <div><span className="label label-info">{matchObj.userID==localStorage.getItem("id")?"me":matchObj.userName}</span></div> */}
                          {/* <div><span className="label label-primary">CTC - {candidate.current_ctc} LPA</span></div> */}
                          <div>
                              <input type="text" placeholder="search here"/>
                          <button className="btn" data-toggle="modal" data-target="#add-job-cand"> 
                          <i className="fa fa-plus" style={{color:"black"}} ></i> Add Candidate</button> 
                          {/* <button className="btn btn-primary">Print</button> */}
                          </div>
                      </div>
                    </div>

                    <div className="tabs row">
                      <ul className="nav nav-pills nav-justified text-center" style={{width:"100%"}}>
                      <li><a href="javascript:void(0)" className={this.state.activeTab=="details"?"active":""} onClick={()=>{
                          this.setState({activeTab:"details"})
                      }}>Details</a></li>
                      <li><a href="javascript:void(0)" className={this.state.activeTab=="resume"?"active":""} onClick={()=>{
                          this.setState({activeTab:"resume"})
                      }}>Resume</a></li>
                      </ul>
                    </div>
                    
                    <div>
                      {
                          this.state.activeTab=="details"?
                          <CandidateDetail currentCandidate={this.props.currentCandidate}/>
                          :<Resume mode="box" currentCandidate={candidate}/>
                      }
                    </div>

                  </div>
                 </div>
               <div className="modal-footer" style={{padding:"5px 15px"}}>
                 <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
               </div>
             </div>
           </div>
               </div>)
               :(this.state.mode=="box"?(
                 <div>
                 <div className="col-xs-12 col-sm-8 main_profile">
                <div className="bg-white row info">
                    <div className="pull-left">
                        <h4>{candidate.candidateName}</h4>
                        <h5>{candidate.work_experience[0].organization}</h5>
                        <h5>{candidate.location}</h5>
                    </div>
                    <div className="pull-right">
                        {/* <div><span className="label label-info">{matchObj.userID==localStorage.getItem("id")?"me":matchObj.userName}</span></div> */}
                        {/* <div><span className="label label-primary">CTC - {candidate.current_ctc} LPA</span></div> */}
                        <div><button className="btn" data-toggle="modal" data-target="#add-job-cand"> 
                        <i className="fa fa-plus" style={{color:"black"}} ></i> Add To Job</button> 
                        {/* <button className="btn btn-primary">Print</button> */}
                        </div>
                    </div>
                </div>

                  <div className="tabs row">
                    <ul className="nav nav-pills nav-justified text-center" style={{width:"100%"}}>
                    <li><a href="javascript:void(0)" className={this.state.activeTab=="details"?"active":""} onClick={()=>{
                        this.setState({activeTab:"details"})
                    }}>Details</a></li>
                    <li><a href="javascript:void(0)" className={this.state.activeTab=="resume"?"active":""} onClick={()=>{
                        this.setState({activeTab:"resume"})
                    }}>Resume</a></li>
                    </ul>
                  </div>
                  
                  <div>
                        <div className={this.state.activeTab=="details"?"":"none"}>
                        <CandidateDetail currentCandidate={this.props.currentCandidate}/></div>
                        <div className={this.state.activeTab=="resume"?"":"none"}>
                        <Resume mode="box" currentCandidate={this.props.currentCandidate}/></div>
                  </div>  
               </div>
               </div>):<h1>error</h1>)
          }
          </div>
        );
    }

    componentDidMount(){
        window.onresize=()=>{
            console.log(this.state.mode);
            if($(window).width()<=768&&this.state.mode!="modal"){
                // console.log("resized","modal");
                this.setState({mode:"modal"});
            }
            else if($(window).width()>768&&this.state.mode!="box"){
                // console.log("resized","box");
                this.setState({mode:"box"});
            }
        }
    }

    componentWillUnmount(){
        window.onresize=null;
    }
    
}


