import React,{Component} from  'react';
const $=window.$;
const constFilter={
    exp_min:"",
    exp_max:"",
    ctc_min:"",
    ctc_max:"",
    location:"",
    college_tier:"",
    mandatory_skills:[],
    other_skills:[],
    domain:atob(localStorage.getItem("token")).split(":")[0].split("@")[1].split(".")[0],
    role:""
};
export default class FiltersBox extends Component{
    constructor(props){
        super(props);
        this.state={
            jobRoles:["Software Developer", "UI Developer","Devops", "Data Scientist","UI Designer","BigData Engineer"],
            jobLocation:["Bangalore","Hyderabad","Chennai","Gurgaon","Delhi/NCR","Mumbai","Pune","Kolkata","Cochin","Trivandrum"],
            filters:constFilter
        };
    }

    componentDidMount(){
        $('[data-toggle="tooltip"]').tooltip();
        //  $('[data-toggle="popover"]').popover()
    }

    render(){
       let  filters = this.state.filters;
        return (
      (<div>
            <div className="modal fade" id="filter-cand" tabIndex="15" role="dialog">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                <div className="modal-header">
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 className="modal-title" id="myModalLabel">Select Filters</h4>
                </div>
                <div className="modal-body new_job">
                <table className="table no-border">
                     <tbody>
                         {/* <tr>
                             <td><label htmlFor="ro">Enter Role</label></td>
                             <td><select name="" id="ro" className="form-control" placeholder="Enter roles" value={filters.role}
                             onChange={(e)=>this.onChange(e,"role")}>
                                <option value="">-- Select Job Role --</option>
                                {this.state.jobRoles.map((obj,index)=><option value={obj} key={index}>{obj}</option>)}
                             </select>
                             </td>
                         </tr> */}
                         <tr>
                             <td><label htmlFor="mainskill">Mandatory Skills</label></td>
                             <td><input type="text" className="form-control" list="sk" placeholder="Enter mandatroy skills"
                             onChange={(e)=>this.onChangeManSkills(e)} id="mainskill"
                             data-toggle="tooltip" data-trigger="focus" data-container="body" data-placement="top"
                             title="Use , to separate skills"/>
                                {/* <datalist id="sk">
                                {this.state.jobRoles.map((obj,index)=><option value={obj} key={index}/>)}
                                </datalist> */}
                             </td>
                         </tr>
                         
                         <tr>
                             <td colSpan="2" className="no-padd">
                             <div className="text-center"> 
                               {filters.mandatory_skills.map((value, index)=> {
                                    return (
                                         <span className="badge text-left" style={{fontSize:14,display:'inline-block',margin:"0px 5px 5px",padding:6,backgroundColor:'blue'}}
                                         key={index}>{value}
                                            <a 
                                             onClick={this.removeFromSkills.bind(this,value,"main")} 
                                            style={{color:'white',padding:8,marginTop:-8,cursor:"pointer",fontSize:14}}><i className="fa fa-times"></i>  
                                            </a>
                                            </span>
                                    )
                                })} 
                                </div>
                                </td> 
                         </tr>
                         <tr>
                            
                             <td><label htmlFor="orskills">Either/OR Skills</label></td>
                             <td><input type="text" placeholder="Enter or skills" className="form-control"
                             onChange={(e)=>this.onChangeOrSkills(e)} id="orskills"
                             data-toggle="tooltip" data-trigger="focus" data-container="body" data-placement="top"
                             title="Use / for either/Or skills and , to separate skills"/></td>
                         </tr>
                         
                         <tr>
                             <td colSpan="2" className="no-padd">
                             <div className="text-center"> 
                               {filters.other_skills.map((value, index)=> {
                                    return (
                                         <span className="badge text-left" style={{fontSize:14,display:'inline-block',margin:"0px 5px 5px",padding:6,backgroundColor:'blue'}}
                                         key={index}>{value}
                                            <a 
                                             onClick={this.removeFromSkills.bind(this,value,"or")} 
                                            style={{color:'white',padding:8,marginTop:-8,cursor:"pointer",fontSize:14}}><i className="fa fa-times"></i>  
                                            </a>
                                            </span>
                                    )
                                })} 
                                </div>
                                </td> 
                         </tr>
                         <tr>
                             <td><label htmlFor="">College</label></td>
                             <td><select name="" id="" className="form-control" 
                             value={filters.college_tier} onChange={(e)=>this.onChangeNum(e,"college_tier")}>
                                 <option value="">All Colleges</option>
                                 <option value="1">Tier 1</option>
                                 <option value="2">Tier 2</option>
                                 <option value="3">Tier 3</option>
                             </select></td>
                         </tr>
                         {/* <tr>
                             <td><label htmlFor="">Current Company</label></td>
                             <td><input type="text" placeholder="Enter current company" className="form-control"/></td>
                         </tr> */}
                         <tr>
                             <td><label htmlFor="">Experience Range</label></td>
                             <td><input type="number" className="form-control exp" min="0" max="99"
                             value={filters.exp_min} onChange={(e)=>this.onChangeNum(e,"exp_min")}/> To <input type="number" className="form-control exp" 
                             min="0" max="99" value={filters.exp_max} onChange={(e)=>this.onChangeNum(e,"exp_max")}/></td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">CTC Range</label></td>
                             <td><input type="number" className="form-control exp" min="0" max="99"
                             value={filters.ctc_min} onChange={(e)=>this.onChangeNum(e,"ctc_min")}/> To <input type="number" className="form-control exp" 
                             min="0" max="99" value={filters.ctc_max} onChange={(e)=>this.onChangeNum(e,"ctc_max")}/></td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">Job Location</label></td>
                             <td> <select name="" id="" className="form-control" 
                             value={filters.location} onChange={(e)=>this.onChange(e,"location")}>
                                <option value=""> -- Anywhere --</option>
                                {this.state.jobLocation.map((obj,index)=><option value={obj} key={index}>{obj}</option>)}
                                </select>
                             </td>
                         </tr>
                        {/* <tr>
                             <td><label htmlFor="">Notice Period</label></td>
                             <td><input type="number" placeholder="Enter notice period" className="form-control" 
                             value={filters.notice_Period} onChange={(e)=>this.onChangeNum(e,"exp_min")}/></td>
                         </tr> */}
                         
                         {/* <tr>
                             <td><label htmlFor="">Last Active</label></td>
                             <td><select name="" id="" className="form-control" >
                                 <option value="">All</option>
                                 <option value="7">A Week Ago</option>
                                 <option value="15">15 Days Ago</option>
                                 <option value="30">30 Days Ago</option>
                                 <option value="45">45 Days Ago</option>
                                 <option value="60">60 Days Ago</option>
                                 <option value="90">90 Days Ago</option>
                             </select></td>
                         </tr> */}
                        </tbody>
                     </table>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    <button type="button" className="btn btn-primary" onClick={this.applyFilters.bind(this)}>Apply</button>
                </div>
                </div>
            </div>
            </div>
        </div>) 
    );
    }

    manageMinMaxPair(obj){
        let filters=JSON.parse(JSON.stringify(obj));
        if((!filters["ctc_min"])&&filters["ctc_max"])
                                    filters["ctc_min"]=0;
        else if(filters["ctc_min"]&&(!filters["ctc_max"]))
                                    filters["ctc_max"]=100;
        else if(filters["ctc_min"]&&(filters["ctc_max"])){}
        else {delete filters["ctc_min"];
             delete filters["ctc_max"];}
        // console.log(filters);
        if((!filters["exp_min"])&&filters["exp_max"])
             filters["exp_min"]=0;
        else if(filters["exp_min"]&&(!filters["exp_max"]))
             filters["exp_max"]=100;
        else if(filters["exp_min"]&&filters["exp_max"]){}
        else {delete filters["exp_min"];
         delete filters["exp_max"];}     
        //  console.log(filters);    
         return filters;                              
    }


    getFilteredFilter(){
        let filters=JSON.parse(JSON.stringify(this.state.filters));
        filters=this.manageMinMaxPair(filters);
        let keys=Object.keys(filters);
        for(let x in keys){
            if(typeof filters[keys[x]] == "number" || typeof filters[keys[x]] == "string"){
                if(keys[x]=="ctc_min"||keys[x]=="ctc_max"||keys[x]=="exp_max"||keys[x]=="exp_min"||keys[x]=="domain")
                    continue;
                if(!filters[keys[x]].toString())
                delete filters[keys[x]];
            }
            else if(filters[keys[x]] instanceof Array)
                 if(!filters[keys[x]].length)
                    delete filters[keys[x]];
        }
        return filters;
        // console.log(filters);
    }

    applyFilters(){
        let filters=this.getFilteredFilter();
        console.log(filters);
        this.props.setActiveFilter(JSON.parse(JSON.stringify(filters)));
        this.props.applyFilter(filters);
        $("#filter-cand").modal('hide');
    }
    
  onChangeManSkills(e){
    let filters=JSON.parse(JSON.stringify(this.state.filters));
    filters.mandatory_skills=e.target.value.toLowerCase().trim().split(",").filter(obj=>{if(obj)return true;});
    this.setState({
        filters
    });
  } 

  removeFromSkills(val,skillType){
    //   alert(val);
    let rejec=new RegExp(","+val,'i');
    let filters=JSON.parse(JSON.stringify(this.state.filters));
      if(skillType=="main"){
        if(filters.mandatory_skills[0]==val){
            rejec=new RegExp(val+",","i");
        } 
        filters.mandatory_skills.splice(filters.mandatory_skills.indexOf(val),1);
        $("#mainskill").val($("#mainskill").val().replace(rejec,''));
      }
      if(skillType=="or"){
        if(filters.other_skills[0]==val){
            rejec=new RegExp(val+",","i");
        }
        filters.other_skills.splice(filters.other_skills.indexOf(val),1)
        $("#orskills").val($("#orskills").val().replace(rejec,''));
      }
    this.setState({
        filters
    }); 
  }

  onChangeOrSkills(e){
    let filters=JSON.parse(JSON.stringify(this.state.filters));
    filters.other_skills=e.target.value.toLowerCase().trim().split(",").filter(obj=>{if(obj)return true;});
    this.setState({
        filters
    });
  }

  onChange(e,key){
    let filters=JSON.parse(JSON.stringify(this.state.filters));
    filters[key] = e.target.value;
    // console.log(e.target.value)
    this.setState({
        filters
    });
  }

  onChangeNum(e,key){
    let filters=JSON.parse(JSON.stringify(this.state.filters));
    filters[key]=parseInt(e.target.value)==null?"":parseInt(e.target.value);
    this.setState({
        filters
    });
  }



}


