import React, {Component} from 'react';
import Accordion from './../RightPanels/Accordion';
class UserInfo extends Component{

  constructor(props){
    super(props);
    this.state={

    };
  }

  render(){
    return (
      <div className="current-chat-profile" style={{height:"calc(100vh - 100px)",overflowY:"auto"}}>
                    <div className="header">
                        <div className="visible-xs-inline pull-left">
                        <a onClick={()=>{
                            this.props.setState({openInfoBox:false})
                            }} className="btn visible-xs-block" style={{color:"black"}}>
                    <i className="fa fa-times"></i></a>
                       </div>
                        <div className="user_thumb">
                            <img src="assets/image/user.png" alt="" className="direct-chat-img"/>
                        </div>
                        <div className="info">
                            <h6>demo</h6>
                            <p>demo</p>
                        </div>
                        <div>
                        <div class="panel-group" id="accordion" role="tablist" aria-multiselectable="true">
                            <Accordion id="1"/>
                            <Accordion id="2"/>
                            <Accordion id="3"/>
                        </div>
                        </div>
                    </div>
       </div>);
  }

}

export default UserInfo;


