import React,{Component} from  'react';
const $=window.$;
export default class CandidatesBox extends Component{

    constructor(props){
        super(props);
        this.state={
        };
    }


    render(){
        return (
      (
       <div className="col-xs-12 col-sm-4 bg-white" style={{paddingRight:10}}>
                    <h4 className="text-center">Candidates</h4>
                    <div className="">      
                     {/* <input type="text" className="form-control" placeholder="Search Candidates" onChange={(e)=>this.props.searchCandidates(e.target.value)}/> */}
                      {/* <div className="input-group-btn"> */}
                          {/* <button className="btn btn-primary"><i className="fa fa-search"></i></button> */}
                      {/* </div> */}
                    </div>
                      <div className="list_box_container" style={{maxHeight:"calc(100vh - 0px)",
                             overflowY:"scroll",
                             marginRight:"-10px"}}>
                      {
                          this.props.candidateList.map((obj,index)=>{
                              if(!obj.work_experience){
                                obj.work_experience=[{
                                    organization:"",
                                    role:""
                                }];
                              }
                              return (
                          <div className={this.props.currentCandidate.candidateContact==obj.candidateContact?"applicant row active bg-white":"applicant row bg-white"}
                          onClick={()=>{this.props.setCurrentCandidate(obj,index)}} key={index} data-toggle="modal" data-target="#cand-info_modal">
                              <div className="user_img pull-left">
                                  <img src="assets/image/user.png" className="img-responsive img-circle" alt={obj.candidateName}/>
                              </div>
                              <div className="applicant_info pull-left">
                                  <h5 className="user_name">{obj.candidateName}</h5>
                                  <h6 className="comp_name">{obj.work_experience[0].organization}</h6>
                                  <h6 className="no-mrg designaton">{obj.work_experience[0].role}</h6>
                              </div>
                          </div>);
                          }
                          )}
                      </div>
                  </div>
                ) 
    );
    }

    componentDidUpdate(){
        
    }
}


