import React,{Component} from  'react';
import FiltersBox from './InvFilters';
import CandidatesBoxes from './CandidateBoxes';
import CandidateInfo from './CandidateInfo';
import URL from './../common/ApiConst';
import swal from 'sweetalert2';
import * as CONSTANTS from './../common/constants';
import AddToJob from './AddToJob';
import StatusFilter from "./../candidates/status/StatusFilter";
const $=window.$;
const ccCandidate={
    // added_by:[{
    //   Timestamp:new Date().getMilliseconds(),
    //   clientName:"",
    //   primarySkill:"",
    //   status:[{status:",Timestamp:Date.now().toString()}]}
    // ],
    candidateContact:"",
    candidateEmail:"",
    candidateName:"",
    resumeText:"",
    work_experience:[{
        organization:"",
        role:""
    }],
    employer:"",
    location:"",
    designation:"",
    current_ctc:"0",
    noticePeriod:"0",
    degree:"",
    college:"0",
    'et/ec':"0",
    "work_summary":{
      "description": "",
      "industry": "",
      "functional_area": "",
      "role": ""
  },
  "education":[],
};


  

export default class InventoryContainer extends Component{

    constructor(props){
        super(props);
        this.state={
            candidateList:[],
            currentCandidate:ccCandidate,
            currentPage:0,
            totalPage:0,
            enteries:0,
            activeFilters:{},
            statusList:CONSTANTS.primaryFilterList,
            activeStatus:CONSTANTS.primaryFilterList[0],
            activeJob:{"cityLocation":"Bengaluru, Karnataka","clientName":"Accenture","creatorID":"mMrgZ3cwcFd4jbToXMO4lzS5pNh2","creatorName":"Ashfaq","invitedUserEmails":[],"isArchived":true,"isInvited":false,"jobID":"M8XQZyWIy4L4ZmfOzVro8V67hxbR3j","mailID":"ashfaq@vlsconsulting.in","maxExpense":5,"minExpense":1,"primarySkill":"Java Developer","subLocation":"Bengaluru","timeCreated":1516104188340,"maxCTC":"","aboutRole":"","questionnaire":[],"isContractual":false,"contractDuration":""}
  
        };
        this.index=0;
    }


    render(){
        return (
      (<div className="nav-bar-manag">
      <div className="candidate_list_container">

      <div className="bg-white job_info row" style={{marginBottom:5}}>
                <div className="pull-left">
              <h3><strong>{this.state.activeJob.clientName}</strong> - {this.state.activeJob.primarySkill}</h3>
                 <h5><span>{this.state.activeJob.cityLocation}</span><span>{this.state.activeJob.minExpense} - {this.state.activeJob.maxExpense} years</span></h5>
              </div>
              <div className="pull-right button-group">
                <button className="btn"><i className="fa fa-check-square-o"></i> Inactive</button>
                <a className="btn btn-primary" href="#/candidates/jobs" onClick={()=>{$(".active_block .active").removeClass("active");
          $("#jb").addClass("active");
          }}><i className="fa fa-eye"></i> View Job</a>
              </div>
         </div>


             <StatusFilter statusList={this.state.statusList}
            activeStatus={this.state.activeStatus}
            updateActiveStatus={(e)=>this.setState({activeStatus:e})}/>

            
            
            <div className="row filters" style={{background:"white",marginTop:0,padding:"10px 5px"}}>
                 <div className="pull-left">
                 <button className="btn" style={{fontSize:16}} onClick={this.prevPage.bind(this)}>
                 <i className="fa fa-angle-left"></i> 
                 </button>
                &nbsp;
                 <button className="btn" style={{fontSize:16}} onClick={this.nextPage.bind(this)}>
                 <i className="fa fa-angle-right"></i> 
                 </button>
                  <h5 style={{margin:0,display:"inline"}}><strong>{this.state.currentPage+1} / {this.state.totalPage+1} Page ,</strong> {this.state.enteries} Results</h5> 
                 
                 </div>
                  <div className="pull-right">
                      <button className="btn" data-toggle="modal" data-target="#excel_modal"><i className="fa fa-download"></i>Download Excel</button>
                      <button className="btn"  data-toggle="modal" data-target="#filter-cand"><i className="fa fa-pencil"></i> Filter</button>
                  </div>
            </div>

           
            <div className="current_container row">
            <CandidatesBoxes candidateList={this.state.candidateList}
                currentCandidate={this.state.currentCandidate}
                searchCandidates={this.searchCandidates.bind(this)}
                setCurrentCandidate={(obj,index)=>{this.setState({currentCandidate:obj}); this.index=index;}}/>
            <CandidateInfo 
            currentCandidate={this.state.currentCandidate} />
            </div>
        </div>
        <FiltersBox applyFilter={this.applyFilter.bind(this)} setActiveFilter={(obj)=>this.setState({activeFilters:obj})}/>
        <AddToJob jobsList={this.props.JobsList} addToJob={this.addToJob.bind(this)}/>    
        </div>) 
    );
}

searchCandidates(val){

}
   
resetCandidateList(){
    let that=this;
      $.ajax({
        url:URL.FILTERS_INVENTORY,
        type:"post",
        data:{    
         "domain":atob(localStorage.getItem("token")).split(":")[0].split("@")[1].split(".")[0],  
          page:0
        }       
      }).done(data=>{
        if(typeof data=="string")data=JSON.parse(data);
        let arr=[];
        for(let x in data.data)
        { 
        data.data[x]["et/ec"]=data.data[x]["et/ec"]==undefined?"":data.data[x]["et/ec"];
        arr.push(data.data[x]);   
        }
        that.setState({
            currentPage:0,
            totalPage:data.pages,
            enteries:data.enteries,
            candidateList:arr,
            currentCandidate:arr[0]
        });
      })
      .fail((err,e,xhr)=>{
        console.log(xhr);
        swal("Error in candidates loading.","Error in loading candidates . ","error");
      });
}

applyFilter(obj,page=0){
    obj.page=page;
    obj.domain=atob(localStorage.getItem("token")).split(":")[0].split("@")[1].split(".")[0];  
          
    let that=this;
    $.ajax({
      url:URL.FILTERS_INVENTORY,
      type:"post",
      data:obj       
    }).done(data=>{
      if(typeof data=="string")data=JSON.parse(data);
      let arr=[];
      that.index=0;
      if(Object.keys(data.data).length){
      for(let x in data.data)
      { 
      data.data[x]["et/ec"]=data.data[x]["et/ec"]==undefined?"":data.data[x]["et/ec"];
      arr.push(data.data[x]);   
      }
      that.setState({
          currentPage:page,
          totalPage:data.pages-1,
          enteries:data.enteries,
          candidateList:arr,
          currentCandidate:arr[0]
      });
    }
    else that.setState({
        currentPage:0,
        totalPage:0,
        enteries:0,
        candidateList:arr,
        currentCandidate:ccCandidate
    });
      console.log(data);
    })
    .fail((err,e,xhr)=>{
      console.log(xhr);
      swal("Error in candidates loading.","Error in loading candidates . ","error");
    });
}

prevPage(){
 let {currentPage,totalPage}=this.state;
    if(currentPage-1>=0&&currentPage-1<totalPage){
        this.applyFilter(this.state.activeFilters,currentPage-1)
    }
}

nextPage(){
    let {currentPage,totalPage}=this.state;
    if(currentPage+1<totalPage){
        this.applyFilter(this.state.activeFilters,currentPage+1)
    }
}

addToJob(array){
    let candidate=JSON.parse(JSON.stringify(this.state.currentCandidate));
  //  console.log(array);
  for(let x in array){
    let obj=array[x];
    var Addedobj={ 
        Timestamp:Date.now(),
        clientName:obj.clientName,
        companyURL:"www."+atob(localStorage.getItem("token")).split(":")[0].split("@")[1],
        domain:atob(localStorage.getItem("token")).split(":")[0].split("@")[1].split(".")[0],
        jobID:obj.jobID,
        position:0,
        primarySkill:obj.primarySkill,
        userEmail:atob(localStorage.getItem("token")).split(":")[0],
        userID:localStorage.getItem("id"),
        userName:localStorage.getItem("name")||atob(localStorage.getItem("token")).split(":")[0].split("@")[0], 
        status:[
          {status:"new:New Resume",
          Timestamp:Date.now(),
          userID:localStorage.getItem("id")
        }]
    }; 
    console.log(Addedobj);
    candidate.added_by=[Addedobj];
    if(x<array.length-1)
    this.props.addCandidate(candidate,5);
    else if(x==array.length-1)
        this.props.addCandidate(candidate);

}
 setTimeout(()=>{},800);
}

componentDidMount(){
  let that=this;        

    // this.resetCandidateList();

    window.onkeydown=(e)=>{
        var key = e.keyCode ? e.keyCode : e.which;
        // alert(this.index)
        if(this.state.candidateList.length){ 
        if(key == 37) {
            this.index=this.index-1>=0?this.index-1:0;
            // console.log(this.state.candidateList[this.index]);
            this.setState({currentCandidate:JSON.parse(JSON.stringify(this.state.candidateList[this.index]))});
        }else if (key == 39){
            if(this.state.candidateList[this.index+1]){
                ++this.index;
                // console.log(this.state.candidateList[this.index]);
                this.setState({currentCandidate:JSON.parse(JSON.stringify(this.state.candidateList[this.index]))});
            }
        }
       }
     }
  } 
  
  componentWillUnmount(){
      window.onkeydown=null;
  }
}


