import React, { Component } from 'react';
import './SingleOption.css';

class SingleOption extends Component {

  constructor(props) {
    super(props);
    this.state = {

    };
  }

  render() {
    
    let marginBottom=this.props.previousBySender=="true"?4:20;
    return (<div>
      <div className={"direct-chat-msg msg_left row "+(this.props.messageBySender=="true"?"msg_right":"")}>
                        <div className={"direct-chat-info clearfix row "+(this.props.messageBySender=="true"?"none":"")}> 
                        <span className="direct-chat-name pull-left">Demo 1</span> </div>
                        <div className="row"> 
                        {this.props.messageBySender=="true"?null:<img src="assets/image/user.png" alt="" className="direct-chat-img"/>}
                            <div className="p message"  style={{marginBottom:marginBottom}}> 
                                <span className="message_box">
                                <span className="">{this.props.text}</span> 
                                <div className="option-wrap">
                                 <div className="comment-option no-select bubble-animate">Great</div>
                                 <div className="comment-option select bubble-animate">Great</div>
                                 <div className="comment-option no-select bubble-animate">Great</div>
                                 {/* <div className="comment-option no-select bubble-animate">Great</div> */}
                                 <div className="comment-option select bubble-animate">Great</div>
                                 <div className="comment-option no-select bubble-animate">Great</div>
                                </div>
                                
                            </span>
                            </div>
                        </div>
       </div>
    </div>);
    
  }

}

export default SingleOption;


