import React, {Component} from 'react';
import './TextMessage.css';
const $=window.$;
class TextMessage extends Component{

  constructor(props){
    super(props);
    this.state={

    };
  }

  render(){
    let marginTop=this.props.previousBySameSender=="true"?4:20;
    // ||this.props.previousBySameSender=="true"
    let obj=this.props.messageObj
    return (<div>
      <div className={"direct-chat-msg msg_left row "+(this.props.messageBySender=="true"?"msg_right":"")} style={{marginTop:marginTop}}>
                        <div className={"direct-chat-info clearfix row "+(this.props.messageBySender=="true"||this.props.previousBySameSender=="true"?"none":"")}> 
                        <span className="direct-chat-name pull-left">{obj.name}</span> </div>
                        <div className="row"> 
                        {this.props.messageBySender=="true"?null:<img src="assets/image/user.png" alt="" className="direct-chat-img"/>}
                            <div className="p" > 
                                <span className="message_box">
                                <span className="message" 
                                data-toggle="tooltip" data-placement={this.props.messageBySender=="true"?"left":"right"} 
                                title={new Date(obj.timestamp).toLocaleString()}
                                >{obj.message}</span> </span>
                            </div>
                        </div>
       </div>
    </div>);
  }

  componentDidMount(){
    
    $(function () {
      $('[data-toggle="tooltip"]').tooltip();
    })
  }

}

export default TextMessage;


