import React,{Component} from  'react';
import URL from './common/ApiConst';
const $=window.$;
export default class HtmlResume extends Component{


    render(){
        let candidate=this.props.currentCandidate;
        return (
        <div>
              <div className="row">  
              {/* <div className="bg-white col-xs-12" style={{marginBottom:10}}>
                          <div className="text-center"><a href="javascript:void(0);">Other Details <i className="fa fa-caret-down"></i></a></div> 
                          <span className="pull-right"><i className="fa fa-pencil"></i></span>  
                          <h5><strong>CTC</strong> - {candidate.current_ctc} LPA</h5>
                          <h5><strong>Notice Period</strong> - {candidate.notice_period}</h5>
                          <h5><strong>Expected CTC</strong> - {candidate.current_ctc}</h5>
                          <h5><strong>Current Location</strong> - {candidate.current_ctc}</h5>
                </div> */}

               <div className="bg-white col-xs-12" style={{marginBottom:10}}>
                   <p><strong>Summary : </strong> {candidate.work_summary.description}</p> 
                </div> 
                {candidate.work_experience?(
                <div className="col-xs-12 no-padd">
                        <div className="bg-white resume_section border-bottom">
                            <h3>Experience 
                              {/* <span className="pull-right"><i className="fa fa-plus"></i></span> */}
                              </h3>
                            <div>  
                            {candidate.work_experience.map((obj,index)=>{
                           return (<div className={index==candidate.work_experience.length-1?"resume_info row":"resume_info row border-bottom"} key={index}>
                                <span className="pull-right">{obj.duration}</span> 
                                <div className="info">
                                    <h5 className="job_title">{obj.role}</h5>
                                    <h5 className="comp">{obj.organization}</h5>
                                    <p className="job_desc">{obj.skills}</p>
                                </div>
                            </div>
                            )
                          })}
                         </div> 
                     </div>
                 </div>):null}

                 {candidate.education?(
                <div className="col-xs-12 no-padd">
                        <div className="bg-white resume_section border-bottom">
                            <h3>Education  
                              {/* <span className="pull-right"><i className="fa fa-plus"></i></span> */}
                              </h3>
                            <div>  
                            {candidate.education.map((obj,index)=>{
                           return (<div className={index==candidate.education.length-1?"resume_info row":"resume_info row border-bottom"} key={index}>
                                <span className="pull-right">{obj.passout}</span> 
                                <div className="info">
                                    <h5 className="job_title">{obj.type}</h5>
                                    <h5 className="comp">{obj.course}</h5>
                                    <p className="job_desc">{obj.institution}</p>
                                </div>
                            </div>
                            )
                          })}
                         </div> 
                     </div>
                 </div>
                 ):null}
      </div>
    </div>   
    );
    }
}


