import React, {Component} from 'react';
import './MessageList.css';
import TextMessage from '../TextMessage/TextMessage';
import SingleOption from '../SingleOption/SingleOption';
import MultiOptions from '../MultiOptions/MultiOptions';
import RangeSelector from '../RangeSelector/RangeSelector';
const $=window.$;

const ChatDate=(props)=>{return (
    <div className="direct-chat-msg msg_left row" style={{padding:15}}>
    <div className="chat-date text-center"> 
        <p> {new Date().toLocaleDateString()}</p>
    </div>
    </div>
);}

class MessageList extends Component{

  constructor(props){
    super(props);
    this.state={

    };
  }

  render(){
    let previousSender="";
    return (<div>
                    <ChatDate/>
                   {this.props.messages.map((obj,index)=>
                   { let messageBySender=(obj.sender==atob(localStorage.getItem("token")).split(":")[0]);
                     let previousBySameSender=(previousSender==obj.sender);
                     previousSender=obj.sender;
                     if(index==0){
                    //  if(obj.sender==atob(localStorage.getItem("token")).split(":")[0])
                     previousBySameSender="false";
                     }
                    //  debugger;
                     return <TextMessage messageObj={obj} 
                     messageBySender={messageBySender.toString()} 
                     previousBySameSender={previousBySameSender.toString()}
                     key={index}/>}
                   )}
                    {/* <SingleOption text="Demo string to test sample"/>
                    <MultiOptions text="Demo string to test sample"/>
                    <RangeSelector text="Demo string to test sample"/> */}
    </div>);
  }

  componentDidMount(){
    $(function () {
      $('[data-toggle="tooltip"]').tooltip();
    })
  }

}

export default MessageList;


