import React, {Component} from 'react';
import ChatListBox from './ChatListBox';
import './ChatList.css';

class ChatList extends Component{

  constructor(props){
    super(props);
    this.state={
      chatIds:[],
      chatList:[]
    };
  }

  componentWillReceiveProps(nextProps){
    if(this.state.chatIds.length!=nextProps.chatIds.length){
      console.log("new char ids",nextProps.chatIds);
      this.setState({chatIds:nextProps.chatIds,
                      chatList:[]});
       this.addListeners(nextProps.chatIds);               
    }
  }

  getStatus(obj){
    if(obj.chatId==this.props.currentChatId)
    return "active";
    else return "";
  }

  render(){
    return (<div>
      
      <div className="chat-list ">
            <div className="header">
               <span className="blue pull-left"><i className="fa fa-cog"></i></span>
                <span className="blue pull-right"><i className="fa fa-pencil"></i></span>
                <div className="text-center">Messesnger</div> 
            </div>
               <div className="recent-chat-list">
                                     
                   <div className="row no-mrg recent-chat-container">
                   <div className="col-xs-12 no-padd">
                      <div className="recent-chat-box" style={{borderTop:"none"}}>
                          <input type="text" className="form-control search_box" placeholder="Search here"/>
                      </div>
                       
                       <div style={{height:"calc(100vh - 148px)",overflowY:"auto"}} className="sep-right">
                         
                         {
                           this.state.chatList.map((obj,index)=>{
                             return <ChatListBox chatStatus={this.getStatus(obj)} 
                              updateCurrentChat={this.props.updateCurrentChat}  
                             chat={obj} key={index}/>})
                         }
                         {
                          //  to be implemented
                           /* <ChatListBox chatStatus="new"/>
                         <ChatListBox chatStatus="active"/>
                        */}
                       </div>
                    </div>
                    
               </div>
        </div>
      </div>
    </div>);
  }

  objectToArray(obj){
    let keys=Object.keys(),arr=[];
    for(let x in keys){
      arr.push(obj[keys[x]]);
    }
    console.log(arr);
    return arr;
  }

  appendList(obj){
    let chatList=JSON.parse(JSON.stringify(this.state.chatList));
    chatList.push(obj);
    this.setState({chatList:chatList});
    this.props.updateChatList(chatList);
  }
  
  updateList(obj,index) {
    let chatList=JSON.parse(JSON.stringify(this.state.chatList));
    chatList[index]=obj;
    this.setState({chatList:chatList});
    this.props.updateChatList(chatList);
  };

  addListeners(list){
    const db= this.props.firebase.database();
    for(let x in list){
       let ref=db.ref(this.props.domain+"/chats/"+list[x])
       ref.off();
       ref.on('value',
       (snapshot)=>{
          
           let data=snapshot.val();

          //  if(x==0){ data.chatId=list[x];
          //   this.props.updateCurrentChat(data);}
           
           if(this.state.chatList[x]){
            //  data.action="update";
            data.chatId=list[x];
           this.updateList(data,x);
          }
           else 
           { 
            //  data.action="add";
            data.chatId=list[x];
             this.appendList(data);
           }
            console.log(snapshot.val());
       });
    }
  }

  componentDidMount(){
    this.addListeners(this.props.chatIds);
  }

  componentDidUpdate(){
    // this.addListeners();
  }


}

export default ChatList;


