import React,{Component} from  'react';
const $=window.$;
export default class AddToJob extends Component{

    constructor(props){
        super(props);
        this.state={
            jobsList:[],
            addedJob:[]
        };
    }

    componentWillReceiveProps(nextProps){
        let jobsList=JSON.parse(JSON.stringify(nextProps.jobsList));
        for(let x in jobsList){
            jobsList["selected"]=false;
        }
        this.setState({jobsList});
    }

    render(){
        return (
      (<div>
            <div className="modal fade" id="add-job-cand" tabIndex="16" role="dialog">
            <div className="modal-dialog" role="document">
                <div className="modal-content">
                <div className="modal-header">
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 className="modal-title" id="myModalLabel">Add Candidate to Job</h4>
                </div>
                <div className="modal-body new_job">
                <table className="table table-bordered table-hovered">
                     <tbody>
                         {
                             this.state.jobsList.map((obj,index)=>{
                 return (<tr key={index}>
                             <td className="text-center">
                             <input type="checkbox" name="" value={JSON.stringify(obj)} 
                             onClick={(e)=>this.addJob(e)}/></td>
                             <td>
                              <h5>{obj.primarySkill+' - '+obj.clientName}</h5>   
                             </td>
                         </tr>);
                        })
                    }
                     </tbody>
                     </table>
                </div>
                <div className="modal-footer">
                    <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
                    <button type="button" className="btn btn-primary" data-dismiss="modal" onClick={this.addToJobs.bind(this)}>Apply</button>
                </div>
                </div>
            </div>
            </div>
        </div>) 
    );
    }

    addJob(e){
        let jobsList=JSON.parse(JSON.stringify(this.state.jobsList));
        let val=JSON.parse(e.target.value);
        let index=jobsList.findIndex((obj)=>{
            return obj.jobID==val.jobID;
        });
        // console.log(index);
        jobsList[index].selected=!jobsList[index].selected;
        this.setState({jobsList});
    }

    addToJobs(){
        this.props.addToJob(this.state.jobsList.filter((obj)=>{return obj.selected;}));
    }

}


