import React,{Component} from  'react';
import Questionaries from './Questionaries';
import Client from './Client';
const $=window.$;
export default class NewJob extends Component{

    constructor(props){
        super(props);
        this.state={
        };
    }

    render(){
        return (<div>
          <Client/>
          <Questionaries/>  
      <div className="new_job_section">
          <div className="col-xs-12 new_job">
              <h3  className="bg-white header">New Job</h3>
              <div className="bg-white row">
                 <div className="row">
                  <div className="col-xs-12 col-sm-6" style={{borderRight:"1px solid #ccc"}}>
                     <table className="table">
                     <tbody>
                         <tr rowSpan="2">
                             <td><label htmlFor="">Existing Client</label></td>
                             <td><select name="" id="" className="form-control">
                                 <option value="flipkart">Flipkart</option>
                                 <option value="">Amazon</option>
                                 <option value="ebay">E-Bay</option>
                             </select>
                             <div className="row" style={{marginTop:8}}>
                             <div className="col-xs-12">
                             <button className="pull-left btn btn-info"><i className="fa fa-edit"></i> Edit</button>
                             <button className="pull-right btn btn-primary" data-toggle="modal" data-target="#create_job"><i className="fa fa-plus"></i> Add</button>
                             </div>
                             </div>
                             </td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">Desination</label></td>
                             <td><input type="text" className="form-control" list="browsers"/>
                                <datalist id="browsers">
                                  <option value="Internet Explorer"/>
                                  <option value="Firefox"/>
                                  <option value="Google Chrome"/>
                                  <option value="Opera"/>
                                  <option value="Safari"/>
                                </datalist>
                             </td>
                         </tr>
                         <tr>
                            
                             <td><label htmlFor="">Mandatory Skills</label></td>
                             <td><input type="text" placeholder="enter skills" className="form-control"/></td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">Expericence</label></td>
                             <td><input type="number" className="form-control exp"/> To <input type="number" className="form-control exp"/></td>
                         </tr>
                         <tr>
                             <td colSpan="2"><button className="btn btn-primary full-width btn-lg text-center" data-toggle="modal" data-target="#questionaries">Create Questionaries</button></td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">Job Location</label></td>
                             <td><input type="text" placeholder="enter job location" className="form-control"/></td>
                         </tr>
                         </tbody>
                     </table>
                     
                  </div>

                  <div className="col-xs-12 col-sm-6">
                      <table className="table">
                      <tbody>
                         {/* <tr>
                             <td><label htmlFor="">New Client</label></td>
                             <td><button className="btn btn-primary full-width btn-lg text-center" data-toggle="modal" data-target="#create_job">Create New Client</button></td>
                         </tr> */}
                         <tr>
                             <td><label htmlFor="">Primary Skills</label></td>
                             <td><input type="text" className="form-control" list="browsers"/>
                                <datalist id="browsers">
                                  <option value="Internet Explorer"/>
                                  <option value="Firefox"/>
                                  <option value="Google Chrome"/>
                                  <option value="Opera"/>
                                  <option value="Safari"/>
                                </datalist>
                             </td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">Either OR Skills</label></td>
                             <td><input type="text" placeholder="enter skills" className="form-control"/></td>
                         </tr>
                         <tr>
                             <td><label htmlFor="">CTC (Lakhs)</label></td>
                             <td><input type="number" className="form-control exp"/> To <input type="number" className="form-control exp"/></td>
                         </tr>
                         <tr>
                            <td></td>
                             <td><button className="btn btn-primary full-width text-center">Attach ID</button></td>
                         </tr>
                         <tr>
                             <td>Is it a drive?</td>
                             <td>
                                 <input type="radio" id="yesdrive" name="drive"/><label htmlFor="yesdrive">Yes</label>
                                 <input type="radio" id="nodrive" name="drive"/><label htmlFor="nodrive">No</label>
                             </td>
                         </tr>
                         
                        <tr>
                             <td><label htmlFor="">Allot to</label></td>
                             <td><input type="text" placeholder="Enter names" className="form-control"/></td>
                         </tr>
{/* <!--                         show this is user selects yes in a drive--> */}
                         <tr>
                             <td>Enter date</td>
                             <td><input type="date" className="form-control"/></td>
                         </tr>
                         </tbody>
                     </table>
                  </div>
                  </div>
                  <div className="row text-center create_job">
                      <button className="btn btn-primary">Create</button>
                  </div>
              </div>
          </div>
      </div>
        </div>);
    }
}


