import React,{Component} from  'react';

export default class StatusFilter extends Component{

    render(){
        return (
            <div style={{margin:"10px auto"}}>
            <div className="tabs row">
                  <ul className="nav nav-pills nav-justified text-center">
                  {this.props.statusList.map((obj,index)=>
                <li className={this.props.activeStatus.optvalue==obj.optvalue?'active':null} key={index}>
               <a href="javascript:void(0)" style={{padding:"10px"}}
                onClick={this.props.updateActiveStatus.bind(null,obj)}>{obj.optvalue}</a></li> 
                )}
                  </ul>
              </div> 
            </div>
        );
    }
}


