import React, { Component } from 'react';
const $=window.$;
class CandidateDetail extends Component {

render(){
    let candidate=this.props.currentCandidate;
    return (<div className="form-horizontal" style={{"padding":"12px",background:"white"}}>
                     
                     
    <div className="form-group">
              <label  className="col-sm-2 control-label">Last Action</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="status" disabled 
                 value={candidate.last_active||""}
                 // {new Date(parseInt(date)).toLocaleDateString()}  
                 placeholder="Status"/>
              </div>
    </div>
    <div className="form-group">
              <label  className="col-sm-2 control-label">Name</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="candidateName"  
                value={candidate.candidateName} disabled  placeholder="candidate Name"/>
              </div>
    </div>
    <div className="form-group">
              <label  className="col-sm-2 control-label">Mail Id</label>

              <div className="col-sm-9">
                <input type="mail" className="form-control" disabled
                value={candidate.candidateEmail} name="candidateEmail" disabled placeholder="Mail Id"/>
              </div>
    </div>     
    <div className="form-group">
              <label  className="col-sm-2 control-label">Mobile Number</label>

              <div className="col-sm-9">
                <input type="tel" className="form-control"  
                value={candidate.candidateContact} name="candidateContact" disabled placeholder="Mobile Number"/>
              </div>
    </div>     
    <div className="form-group">
              <label className="col-sm-2 control-label">Company</label>

              <div className="col-sm-9">
                <input type="text" className="form-control"  disabled 
                value={candidate.work_experience[0].organization}
                 name="company" placeholder="Company"/>
              </div>
    </div>     
    <div className="form-group">
              <label  className="col-sm-2 control-label">Designation</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" disabled
                value={candidate.work_experience[0].role} name="current_role" 
                  placeholder="Designation"/>
              </div>
    </div>     
    <div className="form-group">
              <label  className="col-sm-2 control-label">Experience</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="experience" 
                value={candidate.experience}   disabled
                 placeholder="Experience"/>
              </div>
    </div>     
    <div className="form-group">
              <label  className="col-sm-2 control-label">CTC</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="current_ctc"  disabled
                value={candidate.current_ctc}  placeholder="CTC"/>
              </div>
    </div> 
    <div className="form-group">
              <label  className="col-sm-2 control-label">ET/EC</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="et"  disabled
                value={candidate["et/ec"]}   placeholder="ET/EC"/>
              </div>
    </div>        
    <div className="form-group">
              <label  className="col-sm-2 control-label">Notice Period</label>

              <div className="col-sm-9">
                <input type="number" className="form-control" name="noticePeriod" 
                value={candidate.noticePeriod}  min="0" disabled
                 placeholder="Notice Period"/>
              </div>
    </div>     
    <div className="form-group">
              <label  className="col-sm-2 control-label">University</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="college" 
                value={candidate.college} disabled  placeholder="University"/>
              </div>
    </div>      
    <div className="form-group">
              <label className="col-sm-2 control-label">Degree</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" name="degree" 
                value={candidate.degree} disabled  placeholder="Degree"/>
              </div>
    </div>
    <div className="form-group">
              <label className="col-sm-2 control-label">Location</label>

              <div className="col-sm-9">
                <input type="text" className="form-control" disabled
                value={candidate.location} name="location"  placeholder="Location"/>
              </div>
    </div>
         
</div>);
}


}

export default CandidateDetail;


