import React,{Component} from  'react';
const $=window.$;
export default class NavBar extends Component{

    render(){
        // window.resume=$.parseHTML(this.props.currentCandidate.resumeText.trim());
    return (
  <div className="nav_bar">
       <div id="logged_user" className="row no-mrg bg-white">
        <div className="user_img pull-left">
            <img src="assets/image/user.png" className="img-responsive img-circle" alt="User 1"/>
        </div>
        <div className="user_info pull-left">
            <h5 style={{textTransform:"capitalize"}}>{localStorage.getItem("name")||atob(localStorage.getItem("token")).split(":")[0].split("@")[0]||"Welcome User"}</h5>
            <h6>{atob(localStorage.getItem("token")).split(":")[0]}</h6>
        </div>
      </div>
      <div className="active_block">    
        <a href="#/home" className="bg-white active" id="cand"
        onClick={()=>{this.manageTabs();$("#cand").addClass("active");}}>Candidates</a>
        <a href="#/home/jobs" className="bg-white" id="jb"
        onClick={()=>{this.manageTabs();$("#jb").addClass("active");}}>Jobs</a>
        {/* <a href="#/candidates/groups" className="bg-white" id="gp"
         onClick={()=>{this.manageTabs();$("#gp").addClass("active");}}>Groups</a> */}
         {/* <a href="#/home/groups" className="bg-white" id="inv"
        onClick={()=>{this.manageTabs();$("#inv").addClass("active");}}>Groups</a> */}
        <a href="#/job/545454545" className="bg-white" id="inv"
       onClick={()=>{this.manageTabs();$("#inv").addClass("active");}}>Demo Pipeline</a>
      </div>
      {/* <h5>Sourcing Tool</h5>
      <div className="row">
          <ul className="nav">
              <li ><a href="#"><i className="fa fa-facebook"></i> Facebook</a></li>
              <li><a href="#"><i className="fa fa-twitter"></i> Twitter</a></li>
              <li><a href="#"><i className="fa fa-star"></i> Boolean</a></li>
          </ul>          
      </div> */}
      <h5 className="bg-white">Create New Job <i className="fa pull-right fa-plus btn-primary" 
      onClick={()=>{
          window.location.href="#/newjob";
          this.manageTabs();
          $("#jb").addClass("active");
        //   this.props.setEditModeJob({editMode:false});

      }}></i></h5>
      {/* <h5>Active Jobs</h5>
      <div className="row active_jobs">
          <ul className="nav">
          {
              this.props.JobsList.map((obj,index)=>     
              <li key={index}><a href="javascript:void(0);">
              <i className="fa fa-briefcase" aria-hidden="true"></i> {obj.primarySkill} 
              <span className="">- {obj.clientName}</span></a></li>
         )
          }
          </ul>
      </div> */}
    </div>
    );
    }

    manageTabs(){
        $(".active_block .active").removeClass("active");
        if($(window).width()<=992)
        $(".nav_bar").addClass("translatey100");
    }

    componentDidUpdate(){
        
    }
}


