import React,{Component} from  'react';
const $=window.$;
export default class Client extends Component{

    constructor(props){
        super(props);
      }

    render(){
        return (<div className="modal fade" id="create_job" tabIndex="-10" role="dialog" aria-labelledby="myModalLabel">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
           <div className="modal-header">
               <button className="btn btn-primary btn-lg full-width">Create New Client</button>
           </div>
            <div className="modal-body">
            <table className="table">
              <tbody>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Enter Client Name"/></td>
                </tr>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Linked Url"/></td>
                </tr>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Facebook Url"/></td>
                </tr>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Twitter url"/></td>
                </tr>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Company url"/></td>
                </tr>
                <tr>
                    <td><input type="text" className="form-control" placeholder="Stage of the company"/></td>
                </tr>
                <tr>
                    <td><input type="number" className="form-control" placeholder="Total funding value url"/></td>
                </tr>
                </tbody>
            </table>
            </div>
            <div className="modal-footer">
              <button type="button" className="btn" data-dismiss="modal">Close</button>
              <button type="button" className="btn btn-primary">Create</button>
            </div>
          </div>
        </div>
      </div>
         
        );
    }
}


