import React, {Component} from 'react';
import './App.css';
import './index.css';
import Header from './components/common/Header';
import NavBar from './components/common/NavBar';
import TodoContainer from './components/common/todo/ToDoContainer';
import NewJob from './components/newjob/NewJob';
import {HashRouter as Router  , Route , Link,NavLink,Redirect} from 'react-router-dom';
import ChatShell from './components/chatwindow/ChatShell/ChatShell';
import InventoryContainer from './components/currentjob/InventoryContainer';
class App extends Component{

  constructor(props){
    super(props);
  }

  render(){
    return (<div>
      <Header/>
     <div className=""> 
      

  <Router>
     <div className="">
      
        <Route exact strict path={"/home"} render={(props)=>{
              this.active="candidates";
              return  (<div className="container-fluid main">
                 <NavBar/>
                 <div className="candidate">
                    <div className="container-fluid">     
                      <TodoContainer/>
                    </div>
                 </div>
          </div>)
        } }/> 
            
        <Route path={"/home/jobs"} render={(props)=>{
            return <div>
              <div className="container-fluid main">
                 <NavBar/>
                 <div className="candidate">
                   
                 </div>
          </div>
            </div>
        } }/>
        
        <Route path={"/newjob"} render={(props)=>{return <div> 
          <div className="container-fluid main">
                 <NavBar/>
                 <div className="candidate">
                  <NewJob/>
                 </div>
          </div>
          </div>;
        }} />


      <Route path={"/home/groups"} render={(props)=>{return <div> 
          <div className="container-fluid main">
                 <NavBar/>
                 <div className="candidate">
                  {/* <NewJob/> */}
                 </div>
          </div>
          </div>;
        }} />

      <Route path={"/job/545454545"} render={(props)=>{return <div> 
          <div className="container-fluid main">
                 <NavBar/>
                 <div className="candidate">
                 <InventoryContainer/>
                 </div>
          </div>
          </div>;
        }} />

        <Route path={"/chats"} render={(props)=>{
          return (<div>
            <ChatShell/> 
        </div>);
        }} />

        <Route render={(props)=>{return <Redirect to={"/home"}/>
        }}/>  
     </div>
  </Router>
    </div>
    </div>);
  }

}

export default App;


