import React, {Component} from 'react';
import './MessageShell.css';
import TextMessage from '../TextMessage/TextMessage';
import UserInfo from '../UserInfo/UserInfo';
import MessageList from './../MessageList/MessageList';
import MessageInput from './MessageInput';
const $=window.$;

const ChatDate=(props)=>{return (
    <div className="direct-chat-msg msg_left row" style={{padding:15}}>
    <div className="chat-date text-center"> 
        <p> thursday 2015,21 pm </p>
    </div>
    </div>
);}

class MessageShell extends Component{

  constructor(props){
    super(props);
    this.state={
      openInfoBox:false,
      currentChatId:"",
      currentChat:{},
      messages:[],
      count:15
    };
  }

  componentWillReceiveProps(nextProps){
      console.log("state of messages",this.state)
    if(this.state.currentChatId!=nextProps.currentChatId){
        this.removeListeners();
        this.setState({currentChatId:nextProps.currentChatId,
            currentChat:nextProps.currentChat,
        messages:[]},
        ()=>{this.addListeners(nextProps.currentChatId)}) ;         
    }
  }
   
   sendPushNotification(message){
    $.ajax({
        type:"post",
        url: "https://fcm.googleapis.com/fcm/send",
        "contentType": "application/json",
        "headers":{"Authorization": "key= AIzaSyCBDMoaerPNiSnd0-seI4KADSVujkCWi3A"},
        data:JSON.stringify({
          "notification": {
            "title":this.props.currentChat.title,
            "body":message,
            "icon": "assets/image/user.png",
            "click_action":window.origin
          },
          "to":"dt1lDq_tSds:APA91bGqnfjSqxH4B3G6E44o1-oEntan8f0CaZ6AiBPfj1Rg9R3ODnwxiK-hmL_-C5uoMpspElQ7doM0_KWrx7AeK996NSjnaeKbPTEDC8EPsc-WtTTzNGiqZQmzWeT_l2-geN1HTTqY"
        })
           }).done((data)=>{
            console.log((data));
           }).fail((e,ee,xhr)=>{
               console.log(xhr);
           });
      
   } 

   appendList(obj){
    let messages=JSON.parse(JSON.stringify(this.state.messages));
    messages.push(obj);
    this.setState({messages:messages});
    // this.props.updateMessageList(messages);
  }
  
  updateList(obj,index) {
    let messages=JSON.parse(JSON.stringify(this.state.messages));
    messages[index]=obj;
    this.setState({messages:messages});
    // this.props.updateMessageList(messages);
  };


   addListeners(chatId){
    //    debugger;
    if(!chatId)return ;
    let db=this.props.firebase.database();
    let messageRef=db.ref(this.props.domain+"/messages/"+chatId);

    messageRef.limitToLast(this.state.count).on('value',
       (snapshot)=>{
           let data=snapshot.val();
           let arr=[];
           for(var x in data)
           arr.push(data[x])
           
                if(!document.hasFocus()&&x==this.state.count-1)
                    this.sendPushNotification(data.message);  
           this.setState({messages:arr});
           
            console.log(snapshot.val());  
            // // attach add listener
            // let redundantCounter=0;
            // messageRef.limitToLast(5).on('child_added',
            // (snapshot)=>{
            //     if(redundantCounter==0)return;
            //     ++redundantCounter;
            //     console.log(redundantCounter);
            //     let data=snapshot.val();
            //     if(!document.hasFocus())
            //         this.sendPushNotification(data.message);             
            //     console.log("child added",data)
            //     this.appendList(data)});
       });
    //   messageRef.limitToLast(this.state.count).on('child_changed', setMessage);
   }

   removeListeners(){
    //    debugger;
     if(!this.state.currentChatId)return;  
     let db=this.props.firebase.database();
     db.ref(this.props.domain+"/messages/"+this.state.currentChatId).off();
   }

      
  render(){
    return (
    <div className="current-chat">
      <div className="text-center header">
                
                <div className="blue pull-right" style={{paddingTop:5}}>
                    <a onClick={()=>{this.setState({openInfoBox:!this.state.openInfoBox})}}><i className="fa fa-info-circle"></i></a>
                </div>
                <div  style={{margin:"-5px 0 0px -5px"}} className="visible-xs-inline pull-left">
                    <a onClick={()=>{
                        if(this.state.openInfoBox)
                        this.setState({openInfoBox:!this.state.openInfoBox})
                        
                        else 
                        // if(this.props.isDeviceMobile)
                            this.props.updateCurrentChat({})
                        }} className="btn visible-xs-inline-block" style={{color:"black"}}>
                    <i className="fa fa-arrow-left"></i></a>
                    <img src="assets/image/user.png" alt="User" className="direct-chat-img" style={{width:45,height:45,marginTop:2}}/>
                </div>
                <h6>{(this.props.firebase.auth().currentUser||{name:"user"}).name||atob(localStorage.getItem("token")).split(":")[0].split("@")[0]}</h6>
                <p className="grey_col">Active 5 hrs ago</p>
      </div>
     <div className={"paddinglr10 row "+(this.state.openInfoBox?"open-info":"")}>
                  
        <div className="direct-chat box-warning  direct-chat-warning" >
            <div className="box-body" id="messages-list">
                <div className="direct-chat-messages" style={{height:"calc(100vh - 155px)"}} id="messages-list-cont">
                    
                    <MessageList messages={this.state.messages}/>
                    
                </div>
            
             <MessageInput firebase={this.props.firebase}
             domain={this.props.domain}
             currentChatId={this.state.currentChatId}/> 
            </div>                  
        </div>
       
        <div>   
           <UserInfo setState={(obj)=>this.setState(obj)} isDeviceMobile={this.props.isDeviceMobile}
           updateCurrentChat={this.props.updateCurrentChat}/>
        </div>     
        
        </div>
    </div>);
  }

  componentDidUpdate(){
      $("#messages-list-cont").scrollTop($("#messages-list-cont").height());
  }
   
  componentDidMount(){
      this.addListeners(this.props.currentChatId)
  }


}

export default MessageShell;    


